import React, { Component } from 'react';
import Survey from '../../common/survey/containers/Survey';
import { connect } from 'react-redux';
import ActionAddSurvey from '../components/ActionAddSurvey';

class BrowseSurveyPanel extends Component {
    //class constructor whith given properties
    constructor(props) {
        super(props);

        this.getAllSurveyRender = this.getAllSurveyRender.bind(this);
    }


    getAllSurveyRender() {
        let array_render = [];

        for (var i = 0; i < Object.keys(this.props.surveys).length; i++) {
            var key = Object.keys(this.props.surveys)[i];
            console.log(key);

            array_render.push(
                <div className="panel panel-default">
                    <Survey
                        idSurvey={key} displayMode={"preview"}
                    />
                </div>
            );
        }
        return array_render;
    }




    //render function use to update the virtual dom
    render() {
        let display_list = this.getAllSurveyRender();
        return (
            <div>
                <div className="panel panel-default">
                    <ActionAddSurvey />
                </div>
                {display_list}
            </div>
        );
    }
}



const mapStateToProps = (state, ownProps) => {
    console.log(state);
    return {
        surveys: state.updateModelReducer.surveys,
    }
};

export default connect(mapStateToProps)(BrowseSurveyPanel);


