const uuidv1 = require('uuid/v1');
var ObjectID = require("bson-objectid");

const tempQuestionReducer = (state = { question: "", type: "", answers: {}, collapse: false, answersCollapse: false}, action) => {
   
    switch (action.type) {

/*         case 'OLD_INIT_TEMP_QUESTION':
            const newState2 = {
                question: action.obj.txt, type: action.obj.type, answers: {
                    "0": { "id_answer": "0", "position": "0","txt": ""}} };
            return newState2; */

        case 'INIT_TEMP_QUESTION':
            var newUuid = ObjectID.generate();
            var answers = {};
            answers[newUuid] = { "id_answer": newUuid, "position": 0, "txt": "" };      

            const newState2 = {
                question: "", type: "", answers: answers, collapse: false,
                answersCollapse: false };
            return newState2;

        case 'CHANGE_TYPE_TEMP_QUESTION':
            const newState5 = {
                question: state.question, type: action.obj, answers: state.answers, collapse: state.collapse, answersCollapse: state.answersCollapse};
            return newState5;

        case 'CHANGE_TXT_TEMP_QUESTION':
            const newState6 = {
                question: action.obj, type: state.type, answers: state.answers, collapse: state.collapse, answersCollapse: state.answersCollapse
            };
            return newState6;

        case 'CHANGE_COLLAPSE_TEMP_QUESTION':
            const newState7 = {
                question: state.question, type: state.type, answers: state.answers, collapse: !state.collapse, answersCollapse: state.answersCollapse
            };
            return newState7;

        case 'CHANGE_ANSWERS_COLLAPSE_TEMP_QUESTION':
            const newState8 = {
                question: state.question, type: state.type, answers: state.answers, collapse: state.collapse, answersCollapse: !state.answersCollapse
            };
            return newState8;

        case 'UPDATE_TEMP_QUESTION':
            var newAnswers = {};

            for (var i = 0; i < Object.keys(state.answers).length; i++) {
                var key = Object.keys(state.answers)[i];
                newAnswers[key] = state.answers[key];
                if (action.obj.key == key) {
                    newAnswers[key].txt = action.obj.txt;
                }
            }
            const newState3 = {
                question: state.question, type: state.type, answers: newAnswers, collapse: state.collapse, answersCollapse: state.answersCollapse};
            return newState3;

        case 'ADD_ANSWER':
            var newAnswers = {};
            var newUuid = ObjectID.generate();
        
            for (var i = 0; i < Object.keys(state.answers).length; i++) {
                var key = Object.keys(state.answers)[i];
                newAnswers[key] = state.answers[key];
            }

            console.log(newUuid);

            newAnswers[newUuid] = { "id_answer": newUuid, "position": i, "txt": "" };
            console.log(newAnswers);
            const newState4 = { question: state.question, type: state.type, answers: newAnswers, collapse: state.collapse, answersCollapse: state.answersCollapse};
            return newState4;

        case 'DELETE_ANSWER':
            var newAnswers = {};

            for (var i = 0; i < Object.keys(state.answers).length; i++) {
                var key = Object.keys(state.answers)[i];
                newAnswers[key] = state.answers[key];
            }

            delete newAnswers[action.obj.id_answer];

            const newState9 = { question: state.question, type: state.type, answers: newAnswers, collapse: state.collapse, answersCollapse: state.answersCollapse };
            return newState9;

        default:
            return state;
    }
}
export default tempQuestionReducer;


