import React, { Component } from 'react';
import {Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { connect } from 'react-redux';
import { initSelection,updateSurvey } from '../../../../actions';
var Comm = require('../../../../services/Comm.js');

class EditMetaSurvey extends Component {
    //class constructor whith given properties
    constructor(props) {
        super(props);
        this.comm = new Comm();
        this.state = {
            modal: false,
            title: this.props.surveys[this.props.idSurvey].title,
            description: this.props.surveys[this.props.idSurvey].description,
            theme: this.props.surveys[this.props.idSurvey].theme,
            //remove: false
        };
        this.toggle = this.toggle.bind(this);
        this.submit = this.submit.bind(this);
        this.loadSurveyUpdate = this.loadSurveyUpdate.bind(this);
        this.callbackErr = this.callbackErr.bind(this);
        this.remove = this.remove.bind(this);
        this.changeStatus = this.changeStatus.bind(this);
        this.getStatus = this.getStatus.bind(this);
        this.handleChangeTitle = this.handleChangeTitle.bind(this);
        this.handleChangeDesc = this.handleChangeDesc.bind(this);
        this.handleChangeTh = this.handleChangeTh.bind(this);
    }


    loadSurveyUpdate(data) {
/*         if (this.surveys[this.props.idSurvey].questions.lenght == 1 && this.state.remove) {
            this.setState({
                remove: false
            });
            this.props.dispatch(initSelection());
        } */
        this.props.dispatch(initSelection());
        this.props.dispatch(updateSurvey(data));
    }


    callbackErr(msg) {
        console.error('Network Failure ?');
        console.error(msg);

    }

    remove(){
/*         this.setState({
            remove: true
        }); */
        this.comm.removeSurvey({ "id_survey": this.props.idSurvey }, this.loadSurveyUpdate, this.callbackErr);
    }

    toggle() {
        if (!this.state.modal) {
            this.setState({
                title: this.props.surveys[this.props.idSurvey].title,
                description: this.props.surveys[this.props.idSurvey].description,
                theme: this.props.surveys[this.props.idSurvey].theme,
            });
        }
        this.setState({
            modal: !this.state.modal
        });
    }

    changeStatus() {
        this.comm.changeStatus({ "id_survey": this.props.idSurvey }, this.loadSurveyUpdate, this.callbackErr);
    }

    submit() {
        this.toggle();

        let surveyTmp = {
            "id_survey": this.props.idSurvey,
            "title": this.state.title,
            "theme": this.state.theme,
            "description": this.state.description,
            //"url": this.state.url
        }

        this.comm.editSurvey(surveyTmp, this.loadSurveyUpdate, this.callbackErr);
        //this.props.dispatch(addSurvey(data));

    }

    getStatus(){
        switch (this.props.surveys[this.props.idSurvey].status) {
            case "offline":
                return (
                    <div><span class="glyphicon glyphicon-play"></span>&nbsp; Start Campaign</div>
                );
            case "online":
                return (
                    <div><span class="glyphicon glyphicon-stop"></span>&nbsp; Stop Campaign</div>
                );
        }
    }

    handleChangeTitle(e){
        this.setState({
            title: e.target.value
        });
    }

    handleChangeDesc(e){
        this.setState({
            description: e.target.value
        });
    }

/*     handleChangeUrl(e) {
        this.setState({
            url: e.target.value
        });
    }
 */
    handleChangeTh(e) {
        this.setState({
            theme: e.target.value
        });
    }

    render() {
        let status = this.getStatus();

        return (
            <div>
                <div class="btn-group btn-group-justified" role="group" aria-label="...">
                    <div class="btn-group" role="group">
                        <button type="button" class="btn btn-primary" onClick={this.changeStatus}>{status}</button>
                    </div>
                    <div class="btn-group" role="group">
                        <button type="button" class="btn btn-warning" onClick={this.toggle}><span class="glyphicon glyphicon-pencil"></span>&nbsp; Edit Survey</button>
                    </div>
                    <div class="btn-group" role="group">
                        <button type="button" class="btn btn-danger" onClick={this.remove}><span class="glyphicon glyphicon-remove"></span>&nbsp; Remove Survey</button>
                    </div>
                </div>  
                <Modal isOpen={this.state.modal} fade={false} toggle={this.toggle} className={this.props.className}>
                    <ModalHeader toggle={this.toggle}>Edit survey</ModalHeader>
                    <ModalBody>
                        <form>
                            <div class="form-group">
                                <label>Title</label>
                                <input class="form-control" type="text" value={this.state.title} onChange={this.handleChangeTitle.bind(this)} />
                            </div>
                            <div class="form-group">
                                <label>Theme:</label>
                                <select class="form-control"
                                    value={this.state.theme}
                                    onChange={this.handleChangeTh}
                                >
                                    <option value="beauty">Beauty</option>
                                    <option value="sport">Sport</option>
                                    <option value="fashion">Fashion</option>
                                    <option value="shopping">Shopping</option>
                                </select>
                            </div>
                            <div class="form-group">
                                <label>Description</label>
                                <input class="form-control" type="textarea" value={this.state.description} onChange={this.handleChangeDesc.bind(this)} />
                            </div>
{/*                             <div class="form-group">
                                <label>Description</label>
                                    <input class="form-control" type="textarea" value={this.state.url} onChange={this.handleChangeUrl.bind(this)} />
                            </div> */}
                        </form>
                    </ModalBody>
                    <ModalFooter>
                        <button type="button" class="btn btn-primary" onClick={this.submit}>Submit</button>
                        <button type="button" class="btn btn-secondary" onClick={this.toggle}>Submit</button>
                    </ModalFooter>
                </Modal>
            </div>
        );
    }
}

const mapStateToProps = (state, ownProps) => {
    console.log(state);
    return {
        surveys: state.updateModelReducer.surveys,
    }
};

export default connect(mapStateToProps)(EditMetaSurvey);


