import React, { Component } from 'react';
import AnswerYesNo from '../components/AnswerYesNo';
import EditMetaAnswerString from '../components/EditMetaAnswerString';
import NewMetaAnswerString from '../components/NewMetaAnswerString';
import AnswerString from '../components/AnswerString';
import { connect } from 'react-redux';


class Answer extends Component {
    //class constructor whith given properties
    constructor(props) {
        super(props);
        this.getTypeAnswer = this.getTypeAnswer.bind(this);
    }

    getTypeAnswer(){
  
        switch (this.props.mode) {
            case "display":
                switch (this.props.surveys[this.props.idSurvey].questions[this.props.idQuestion].question_type) {
                    case "YesNo":
                        return (
                            <AnswerYesNo />
                        );
                    default:
                        return (
                            <AnswerString idSurvey={this.props.idSurvey} idQuestion={this.props.idQuestion} />
                        );
                }                
            case "new":
                switch (this.props.tempTypeQuestion) {
                    case "YesNo":
                        return (
                            <AnswerYesNo />
                        );
                    default:
                        return (
                            <NewMetaAnswerString/>
                        );
                }

            case "edit":
                switch (this.props.editQuestions[this.props.idQuestion].question_type) {
                    case "YesNo":
                        return (
                            <AnswerYesNo />
                        );
                    default:
                        return (
                            <EditMetaAnswerString idQuestion={this.props.idQuestion}/>
                        );
                }

            default:
                break;
        }


    }

    render() {
        let render_visual = this.getTypeAnswer();

        return (
            <div>
                {render_visual}
            </div>
        );
    }

}
const mapStateToProps = (state, ownProps) => {
    return {
        surveys: state.updateModelReducer.surveys,
        tempTypeQuestion: state.tempQuestionReducer.type,
        tempAnswers: state.tempQuestionReducer.answers,
        editQuestions: state.selectedReducer.edit_questions
    }
};

export default connect(mapStateToProps)(Answer);


