import React, { Component } from 'react';
import { connect } from 'react-redux';
import { updateTempQuestion, addAnswer, deleteAnswer } from '../../../../actions';

class NewMetaAnswerString extends Component {
    //class constructor whith given properties
    constructor(props) {
        super(props);
        this.state = {
            answers: {}
        }
        this.getAnswers = this.getAnswers.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.addAnswer = this.addAnswer.bind(this);
        this.deleteAnswer = this.deleteAnswer.bind(this);
    }

    handleChange(name, e) {
        this.props.dispatch(updateTempQuestion({ key: name, txt: e.target.value }));
    }

    addAnswer() {
        this.props.dispatch(addAnswer());
    }

    deleteAnswer(idAnswer, e) {
        this.props.dispatch(deleteAnswer({id_answer: idAnswer }));
    }

    getAnswers() {
        let array_render = [];
        for (var i = 0; i < Object.keys(this.props.tempAnswers).length; i++) {
            var key = Object.keys(this.props.tempAnswers)[i];

            array_render.push(
                <div class="list-group-item">
                    <div class="input-group">
                        <input type="text" name='txt' value={this.props.tempAnswers[key].txt} onChange={this.handleChange.bind(this, key)} class="form-control" placeholder="Answer" />

                        <span class="input-group-btn">
                            <button type="button" class="btn btn-danger glyphicon glyphicon-trash" onClick={this.deleteAnswer.bind(this, this.props.tempAnswers[key].id_answer)}></button>
                        </span>

                    </div>
                </div>
            );
        }
        return array_render;
    }

    render() {
        let render = this.getAnswers();
        return (
            <div>
                {render}
                <div class="list-group-item">
                    <button type="button" class="btn btn-primary btn-block" onClick={this.addAnswer}>+</button>
                </div>
            </div>

        );
    }
}

const mapStateToProps = (state, ownProps) => {
    console.log(state);
    return {
        tempAnswers: state.tempQuestionReducer.answers,
    }
};

export default connect(mapStateToProps)(NewMetaAnswerString);


