import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Pie, Radar, Line } from "react-chartjs-2";
import { initGlobaleStats } from '../../../actions';

import '../assets/css/font-awesome.min.css';
import '../assets/sass/light-bootstrap-dashboard.css';
import '../assets/css/pe-icon-7-stroke.css';

import {Card} from '../components/Card';
import {StatsCard} from '../components/StatsCard';

class Dashboard extends Component {
    constructor(props) {
        super(props);
        this.backgroundColor = [
                        "#2ecc71",
                        "#3498db",
                        "#95a5a6",
                        "#9b59b6",
                        "#f1c40f",
                        "#e74c3c",
                        "#34495e"],
        this.lightColor = [
                "rgba(46, 204, 113, 0.3)",
                "rgba(52, 152, 219, 0.4)",
                "rgba(149, 165, 166, 0.6)",
                "rgba(155, 89, 182, 0.8)",
                "rgba(241, 196, 15, 0.6)",
                "rgba(231, 76, 60, 0.7)",
                "rgba(52, 73, 94, 0.9)"],
        this.fakeDate = [
            "19/01",
            "20/01",
            "21/01",
            "22/01",
            "23/01",
            "24/01",
            "25/01"
        ]
        this.randomValue = [3,5,6,7,1,4,8,9,2,10]
        this.getDistSurvey = this.getDistSurvey.bind(this);
        this.getAnswersSurvey = this.getAnswersSurvey.bind(this);
        this.getVolumeAnswersByDay = this.getVolumeAnswersByDay.bind(this);
    }

    getDistSurvey(){
        let chartData = {
            "labels": [],
            "datasets": [
                {
                    label: "answers",
                    data: [],
                    backgroundColor: []
                }
            ]
        }

        console.log(this.props.stats.themes);
        
        if (this.props.stats.status) {
            for (var i = 0; i < Object.keys(this.props.stats.themes).length; i++) {
                var key = Object.keys(this.props.stats.themes)[i];
                chartData.labels.push(key);
                chartData.datasets[0].data.push(this.props.stats.themes[key].nb_surveys);
                chartData.datasets[0].backgroundColor.push(this.backgroundColor[i]);
            }
        }

        var options = {
/*             title: {
                display: true,
                text: "Surveys distribution",
                fontSize: 25
            }, */
            legend: {
                display: true,
                position: 'right'
            }
        }
        return (<Pie data={chartData} options={options} />)

    }

    getVolumeAnswersByDay(){
        let chartData = {
            "labels": this.fakeDate,
            "datasets": [
            ]
        }

        console.log(this.props.stats.themes);
        
        if (this.props.stats.status) {
            for (var i = 0; i < Object.keys(this.props.stats.themes).length; i++) {
                var dataset = {}
                var key = Object.keys(this.props.stats.themes)[i];

                dataset["label"] = key;
                dataset["fill"] = true;
                dataset["data"] = [];
                this.fakeDate.forEach(element => {
                    dataset["data"].push(this.randomValue[Math.floor(Math.random() * this.randomValue.length)]);
                });
                dataset["backgroundColor"] = this.lightColor[i];

                chartData.datasets.push(dataset);
            }
        }

        var options = {
/*             title: {
                display: true,
                text: "Surveys distribution",
                fontSize: 25
            }, */
            legend: {
                display: true,
                position: 'right'
            }
        }
        return (<Line data={chartData} options={options} />)

    }

    getAnswersSurvey() {
        let chartData = {
            "labels": [],
            "datasets": [
                {
                    label: "number of surveys",
                    data: [],
                    backgroundColor: "rgba(46, 204, 113, 0.5)",
                },
                {
                    label: "number of answers",
                    data: [],
                    backgroundColor: "rgba(155, 89, 182, 0.5)",
                }
            ]
        }

        console.log(this.props.stats.themes);

        if (this.props.stats.status) {
            for (var i = 0; i < Object.keys(this.props.stats.themes).length; i++) {
                var key = Object.keys(this.props.stats.themes)[i];
                chartData.labels.push(key);
                chartData.datasets[0].data.push(this.props.stats.themes[key].nb_surveys/this.props.stats.nb_surveys);
                chartData.datasets[1].data.push(this.props.stats.themes[key].nb_answers/this.props.stats.nb_answers);
            }
        }

        var options = {
            /*             title: {
                            display: true,
                            text: "Surveys distribution",
                            fontSize: 25
                        }, */
            legend: {
                display: true,
                position: 'right'
            }
        }
        return (<Radar data={chartData} options={options} />)

    }


    render() {
        var pie_content = this.getDistSurvey();
        var radar_content = this.getAnswersSurvey();
        var line_content = this.getVolumeAnswersByDay();
        return (
            <div className="content">
                <div class="row">
                    <div class="col-lg-3 col-sm-6">
                        <StatsCard
                            bigIcon={<i className="pe-7s-paper-plane text-success"></i>}
                            statsText="Online"
                            statsValue={this.props.stats.online}
                            statsIcon={<i className="fa fa-refresh"></i>}
                            statsIconText="Updated now"
                        />
                    </div>
                    <div class="col-lg-3 col-sm-6">
                        <StatsCard
                            bigIcon={<i className="pe-7s-power text-warning"></i>}
                            statsText="Offline"
                            statsValue={this.props.stats.offline}
                            statsIcon={<i className="fa fa-refresh"></i>}
                            statsIconText="Updated now"
                        />
                    </div>
                    <div class="col-lg-3 col-sm-6">
                            <StatsCard
                                bigIcon={<i className="pe-7s-note text-info"></i>}
                                statsText="Done"
                                statsValue={this.props.stats.completed_survey}
                                statsIcon={<i className="fa fa-refresh"></i>}
                                statsIconText="Updated now"
                            />
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-6">
                            <Card
                                statsIcon="fa fa-history"
                                id="chartHours"
                                title="Volume of answers by day"
                                category="What is the best day of the last week?"
                                stats="Last week data"
                                content={
                                    <div className="ct-chart">
                                        {line_content}
                                    </div>
                                    }
                            />
                    </div>
                    <div class="col-md-6">
                            <Card
                                statsIcon="fa fa-refresh"
                                title="Surveys distribution"
                                category="How much surveys by theme?"
                                stats="Updated now"
                                content={
                                    <div id="chartPreferences" className="ct-chart ct-perfect-fourth">
                                      {pie_content}
                                    </div>
                                }
                            />
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-6">
                            <Card
                                id="chartActivity"
                                title="Comparaison"
                                category="Average for each theme"
                                stats="Data information certified"
                                statsIcon="fa fa-check"
                                content={
                                    <div className="ct-chart">
                                        {radar_content}
                                    </div>
                                }

                            />
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state, ownProps) => {
    console.log(state);
    return {
        stats: state.updateModelReducer.stats,
    }
};

export default connect(mapStateToProps)(Dashboard);



