import React from 'react';
import Answer from '../../answer/containers/Answer';
import { Card, CardBody} from 'reactstrap';

class AbstractAnswer extends React.Component {

    constructor(props) {
        super(props);

        this.getRenderByMode = this.getRenderByMode.bind(this);
    }

    getRenderByMode(){
        return null;
    }

  

    render() {
        let render = this.getRenderByMode();
        return (
            
            <Card>
                <CardBody>
                    <Answer mode={this.props.mode} idSurvey={this.props.idSurvey} idQuestion={this.props.idQuestion}/>
                </CardBody>
            </Card>
            
        );
    }
}

export default AbstractAnswer;


