import React, { Component } from 'react';
import { Card, CardTitle, CardSubtitle, Jumbotron} from 'reactstrap';
import { setSelectedSurvey } from '../../../../actions';
import { connect } from 'react-redux';
import EditMetaQuestion from '../../question/components/EditMetaQuestion';

class Visual extends Component {
    constructor(props) {
        super(props);
        this.updateSelectedSurvey = this.updateSelectedSurvey.bind(this);
        this.getStatus = this.getStatus.bind(this);
        
    }

    updateSelectedSurvey() {
        //console.log("Click on -> setSelectedSurvey");
        this.props.dispatch(setSelectedSurvey({ "id": this.props.idSurvey, "key_questions": Object.keys(this.props.surveys[this.props.idSurvey].questions)}));
    }

    getStatus() {
        let status = {};
        if (this.props.surveys[this.props.idSurvey].status == "online")  {
            status.type = "online";
            status.color = "success";
            
        } else {
            status.type = "offline";
            status.color = "warning";
        }

        return status;
    }

    render() {
        let status = this.getStatus();
        let render_visual;
        switch (this.props.displayMode) {

            case "preview":
                render_visual = (
                    <div className="panel-body">
                        <div class="row">
                            <div class="col-sm-9">
                                <Card body>
                                    <CardTitle><span class="badge">{this.props.surveys[this.props.idSurvey].nb_answers}</span> {this.props.surveys[this.props.idSurvey].title}</CardTitle>
                                </Card>
                            </div>
                            <div class="col-sm-3">
                                <Card body>
                                    <button type="button" style={{ cursor: 'default' }} class={"btn btn-sm btn-" + status.color}>{status.type}</button>
                                </Card>
                            </div>
                        </div>
                        <CardSubtitle>{this.props.surveys[this.props.idSurvey].description}</CardSubtitle>                
                    </div>
                );
                break;

            case "resume":
            case "full":
                render_visual = (
                    <Jumbotron>
                        <h1 className="display-3">{this.props.surveys[this.props.idSurvey].title}</h1>
                        <p className="lead">{this.props.surveys[this.props.idSurvey].description}
                        </p>
                    </Jumbotron>

                );
                break;
                
            case "preview_old":
                render_visual = (
                    <div>
                        <h1>{this.props.surveys[this.props.idSurvey].title}</h1>
                        <h5>ID: {this.props.surveys[this.props.idSurvey].id_survey}</h5>
                    </div>
                );
                break;
        }


        return (
            <div onClick={this.updateSelectedSurvey}>
                {render_visual}
            </div>
        );
    }
}

const mapStateToProps = (state, ownProps) => {
    console.log(state);
    return {
        surveys: state.updateModelReducer.surveys,
    }
};

export default connect(mapStateToProps)(Visual);


