import React, { Component } from 'react';
import AbstractAnswer from '../components/AbstractAnswer';
import Answer from '../../answer/containers/Answer';
import { updateTxtSelectedQuestion, updateTypeSelectedQuestion, editQuestion, changeQuestionStatus, updateSurvey} from '../../../../actions';


import { connect } from 'react-redux';
import { Card, Dropdown, DropdownToggle, DropdownMenu } from 'reactstrap';
//import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';

var Comm = require('../../../../services/Comm.js');

class EditMetaQuestion extends Component {
    //class constructor whith given properties
    constructor(props) {
        super(props);
        this.comm = new Comm();

        this.state = {
            dropdownOpen: false
        };
    
        this.toggle = this.toggle.bind(this);
        this.editQuestion = this.editQuestion.bind(this);
        this.close = this.close.bind(this);
        this.editTxtQuestion = this.editTxtQuestion.bind(this);
        this.editTypeQuestion = this.editTypeQuestion.bind(this);
        this.editTypeYesNoQuestion = this.editTypeYesNoQuestion.bind(this);
        this.editTypeUniqueQuestion = this.editTypeUniqueQuestion.bind(this);
        this.editTypeMultipleQuestion = this.editTypeMultipleQuestion.bind(this);
        this.getOthersTypes = this.getOthersTypes.bind(this);
        this.loadSurveyUpdate = this.loadSurveyUpdate.bind(this);
        this.callbackErr = this.callbackErr.bind(this);

    }

    toggle() {
        this.setState({
            dropdownOpen: !this.state.dropdownOpen
        });
    }

    editTxtQuestion(e) {
        this.props.dispatch(updateTxtSelectedQuestion({ id_question: this.props.idQuestion, txt: e.target.value }));
    }

    editTypeQuestion(type) {
        this.props.dispatch(updateTypeSelectedQuestion({ id_question: this.props.idQuestion, type: type}));
    }

    editTypeYesNoQuestion() {
        //console.log("YesNo");
        this.editTypeQuestion("YesNo");
    }
    editTypeUniqueQuestion() {
        console.log("Unique");
        this.editTypeQuestion("unique");
    }
    editTypeMultipleQuestion() {
        //console.log("Multiple");
        this.editTypeQuestion("multiple");
    }

    getOthersTypes() {

        switch (this.props.questions[this.props.idQuestion].question_type) {
            case "YesNo":
                return (
                    <DropdownMenu>
                        <li><a style={{ cursor: 'pointer' }} onClick={this.editTypeUniqueQuestion}>Unique</a></li>
                        <li><a style={{ cursor: 'pointer' }} onClick={this.editTypeMultipleQuestion}>Multiple</a></li>
                    </DropdownMenu>
                );

            case "multiple":

                return (
                    <DropdownMenu>
                        <li><a style={{ cursor: 'pointer' }} onClick={this.editTypeUniqueQuestion}>Unique</a></li>
                        <li><a style={{ cursor: 'pointer' }} onClick={this.editTypeYesNoQuestion}>YesNo</a></li>
                    </DropdownMenu>
                );

            case "unique":

                return (
                    <DropdownMenu>
                        <li><a style={{ cursor: 'pointer' }} onClick={this.editTypeYesNoQuestion}>YesNo</a></li>
                        <li><a style={{ cursor: 'pointer' }} onClick={this.editTypeMultipleQuestion}>Multiple</a></li>
                    </DropdownMenu>
                );
        
            default:
                return ("error type");
                break;
        }

    }

    editQuestion(){

        let answers = {};

        if(this.props.questions[this.props.idQuestion].question_type != "YesNo"){
            answers = this.props.questions[this.props.idQuestion].answers;
        }
        
        let editedQuestion = {
            id_survey : this.props.idSurvey,
            id_question: this.props.idQuestion,
            txt: this.props.questions[this.props.idQuestion].txt,
            type: this.props.questions[this.props.idQuestion].question_type,
            position: this.props.questions[this.props.idQuestion].position,
            answers: answers
        };

        this.comm.editQuestion(editedQuestion, this.loadSurveyUpdate, this.callbackErr)

/*         this.props.dispatch(editQuestion(editedQuestion));
        this.props.dispatch(changeQuestionStatus({ "id_question": this.props.idQuestion })) */

    }

    loadSurveyUpdate(data) {
        this.props.dispatch(updateSurvey(data));
        this.props.dispatch(changeQuestionStatus({ "id_question": this.props.idQuestion }))
    }


    callbackErr(msg) {
        console.error('Network Failure ?');
        console.error(msg);

    }

    close(){
        this.props.dispatch(changeQuestionStatus({ "id_question": this.props.idQuestion }))
    }

    render() {
        let choice_type = this.getOthersTypes();
        return (
            <div>
                <div class="panel-body">
                    <div class="col-sm-9">
                        <Card body>
                            <input type="text" name='txt' value={this.props.questions[this.props.idQuestion].txt} onChange={this.editTxtQuestion.bind(this)} class="form-control" />
                        </Card>
                    </div>
                    <div class="col-sm-3">
                        <Card body>
                            <Dropdown isOpen={this.state.dropdownOpen} toggle={this.toggle}>
                                <DropdownToggle>
                                    {this.props.questions[this.props.idQuestion].question_type} <span class="caret"></span>
                                </DropdownToggle>
                                {choice_type}
                            </Dropdown>
                        </Card>
                    </div>
                </div>

                <div class="panel panel-default">
                    <div class="panel-heading">
                        <h3 class="panel-title">Answers</h3>
                    </div>
                    <Answer mode="edit" idQuestion={this.props.idQuestion}/>
                </div>
                <div class="btn-group" role="group" aria-label="...">
                    <button type="button" class="btn btn-primary glyphicon glyphicon-ok" onClick={this.editQuestion}></button>
                    <button type="button" class="btn btn-default glyphicon glyphicon-remove" onClick={this.close}></button>
                </div>
            </div>

        );

    }
}

const mapStateToProps = (state, ownProps) => {
    console.log(state);
    return {
        surveys: state.updateModelReducer.surveys,
        questions: state.selectedReducer.edit_questions,
    }
};

export default connect(mapStateToProps)(EditMetaQuestion);


