import React, { Component } from 'react';


class AnswerYesNo extends Component {
    //class constructor whith given properties
    constructor(props) {
        super(props);
    }


    render(){
        return (
            <div>
                <div class="list-group-item">Yes</div>
                <div class="list-group-item">No</div>
            </div>
        );
    }
}
export default AnswerYesNo;


