export const updateSurvey = (survey_obj) => {
    return {
        type: 'UPDATE_SURVEY',
        obj: survey_obj
    };
}

export const setSelectedSurvey = (survey_id) => {
    return {
        type: 'UPDATE_SELECTED_ID_SURVEY',
        obj: survey_id
    };
}

