const uuidv1 = require('uuid/v1');
var ObjectID = require("bson-objectid");

const selectedReducer = (state = { status: false, id_survey: {}, dragged_elt: {}, questions_status: {}, edit_questions: {}}, action) => {
    console.log(action);
    switch (action.type) {
        case 'UPDATE_SELECTED_ID_SURVEY':
            var newQuestionStatus = {};
            action.obj.key_questions.forEach(key => {
                newQuestionStatus[key] = "display";
            });

            const newState2 = { status: "edit", id_survey: action.obj.id, questions_status: newQuestionStatus, edit_questions: {}, stats: {status: false}};
            console.log(action.obj.id);
            return newState2;

        case 'INIT_SELECTION':
            console.log("INIT");
            const newState3 = { status: action.obj, id_survey: state.id_survey, questions_status: state.questions_status, stats: state.stats};
            console.log(action.obj);
            return newState3;

        case 'ADD_QUESTION_STATUS':
            var newQuestionStatus = {};

            for (var i = 0; i < Object.keys(state.questions_status).length; i++) {
                var key = Object.keys(state.questions_status)[i];
                newQuestionStatus[key] = state.questions_status[key];
            }

            newQuestionStatus[action.obj] = "new";

            const newState5 = { status: state.status, id_survey: state.id_survey, questions_status: newQuestionStatus, edit_questions: state.edit_questions, stats: state.stats };
            return newState5;

        case 'SAVE_QUESTION_STATUS':
            var newQuestionStatus = {};

            var oldKey = Object.keys(state.questions_status);

            action.obj.key_questions.forEach(key => {
                if(oldKey.includes(key)){
                    newQuestionStatus[key] = state.questions_status[key];
                } else {
                    newQuestionStatus[key] = "display";
                }
            });

            const newState6 = { status: state.status, id_survey: state.id_survey, questions_status: newQuestionStatus, edit_questions: state.edit_questions, stats: state.stats };
            return newState6;


        case 'CHANGE_QUESTION_STATUS':
            var newQuestionStatus = {};

            for (var i = 0; i < Object.keys(state.questions_status).length; i++) {
                var key = Object.keys(state.questions_status)[i];
                if (key != action.obj.id_question) {
                    newQuestionStatus[key] = state.questions_status[key];
                } else {
                    switch (state.questions_status[key]) {
                        case "display":
                            var newQuestion = {};
                            for (var j = 0; j < Object.keys(action.obj.question).length; j++) {
                                var keyOnDict = Object.keys(action.obj.question)[j];
                                if (keyOnDict != "answers") {
                                    newQuestion[keyOnDict] = action.obj.question[keyOnDict];
                                } else {  
                                    var newAnswers = {};                            
                                    for (var k = 0; k < Object.keys(action.obj.question.answers).length; k++) {
                                        var keyAnswer = Object.keys(action.obj.question.answers)[k];
                                        var newAnswer = {};

                                        for (var l = 0; l < Object.keys(action.obj.question.answers[keyAnswer]).length; l++) {
                                            var keyOnAnswer = Object.keys(action.obj.question.answers[keyAnswer])[l];
                                            newAnswer[keyOnAnswer] = action.obj.question.answers[keyAnswer][keyOnAnswer];
                                        }

                                        newAnswers[keyAnswer] = newAnswer
                                    }
                                    newQuestion["answers"] = newAnswers;
                                }
                            }
                            
                            newQuestionStatus[key] = "edit";
                            state.edit_questions[key] = newQuestion;

                            break;

                        case "edit":
                            
                            newQuestionStatus[key] = "display";
                            delete state.edit_questions[key];

                            break;
                     
                        default:
                            break;
                     }
                 } 
                
            }

            const newState4 = { status: state.status, id_survey: state.id_survey, questions_status: newQuestionStatus, edit_questions: state.edit_questions, stats: state.stats};
            console.log(action.obj);
            return newState4;

        case 'UPDATE_ANSWERS_SELECTED_QUESTION':
           
            var newEdit = {};

            for (var i = 0; i < Object.keys(state.edit_questions).length; i++) {
                var key = Object.keys(state.edit_questions)[i];
                newEdit[key] = state.edit_questions[key];
                if (action.obj.id_question == key) {

                    for (var j = 0; j < Object.keys(state.edit_questions[key]).length; j++) {
                        var keyOnDict = Object.keys(state.edit_questions[key])[j];
                        newEdit[key][keyOnDict] = state.edit_questions[key][keyOnDict];

                        if (keyOnDict == "answers") {
                            for (var k = 0; k < Object.keys(state.edit_questions[key].answers).length; k++) {
                                var keyAnswer = Object.keys(state.edit_questions[key].answers)[k];
                                if (action.obj.key == keyAnswer) {
                                    newEdit[key].answers[keyAnswer].txt = action.obj.txt;
                                }
                            }

                        }

                    }
                }
            }

            const newState7 = { status: state.status, id_survey: state.id_survey, questions_status: state.questions_status, edit_questions: newEdit, stats: state.stats }
            return newState7
            ;
        case 'UPDATE_TXT_SELECTED_QUESTION':
           
            var newEdit = {};

            for (var i = 0; i < Object.keys(state.edit_questions).length; i++) {
                var key = Object.keys(state.edit_questions)[i];
                newEdit[key] = state.edit_questions[key];
                if (action.obj.id_question == key) {

                    for (var j = 0; j < Object.keys(state.edit_questions[key]).length; j++) {
                        var keyOnDict = Object.keys(state.edit_questions[key])[j];
                        newEdit[key][keyOnDict] = state.edit_questions[key][keyOnDict];

                        if (keyOnDict == "txt") {
                            newEdit[key].txt = action.obj.txt;
                        }

                    }
                }
            }

            const newState8 = { status: state.status, id_survey: state.id_survey, questions_status: state.questions_status, edit_questions: newEdit, stats: state.stats }
            return newState8;

        case 'UPDATE_TYPE_SELECTED_QUESTION':

            var newEdit = {};

            for (var i = 0; i < Object.keys(state.edit_questions).length; i++) {
                var key = Object.keys(state.edit_questions)[i];
                newEdit[key] = state.edit_questions[key];
                if (action.obj.id_question == key) {

                    for (var j = 0; j < Object.keys(state.edit_questions[key]).length; j++) {
                        var keyOnDict = Object.keys(state.edit_questions[key])[j];
                        newEdit[key][keyOnDict] = state.edit_questions[key][keyOnDict];

                        if (keyOnDict == "question_type") {
                            newEdit[key].question_type = action.obj.type;
                        }

                    }
                }
            }

            const newState9 = { status: state.status, id_survey: state.id_survey, questions_status: state.questions_status, edit_questions: newEdit, stats: state.stats }
            return newState9;

        case 'ADD_ANSWER_SELECTED_QUESTION':

            var newEdit = {};

            var newUuid = ObjectID.generate();

            for (var i = 0; i < Object.keys(state.edit_questions).length; i++) {
                var key = Object.keys(state.edit_questions)[i];
                newEdit[key] = state.edit_questions[key];
                if (action.obj.id_question == key) {

                    for (var j = 0; j < Object.keys(state.edit_questions[key]).length; j++) {
                        var keyOnDict = Object.keys(state.edit_questions[key])[j];
                        newEdit[key][keyOnDict] = state.edit_questions[key][keyOnDict];

                        if (keyOnDict == "answers") {
                            var size = Object.keys(state.edit_questions[key].answers).length;
                            newEdit[key].answers[newUuid] = { "id_answer": newUuid, "position": size.toString(), "txt": "" }; 
                        }

                    }
                }
            }

            const newState10 = { status: state.status, id_survey: state.id_survey, questions_status: state.questions_status, edit_questions: newEdit, stats: state.stats }
            return newState10;

        case 'DELETE_ANSWER_SELECTED_QUESTION':

            var newEdit = {};

            for (var i = 0; i < Object.keys(state.edit_questions).length; i++) {
                var key = Object.keys(state.edit_questions)[i];
                newEdit[key] = state.edit_questions[key];
                if (action.obj.id_question == key) {

                    for (var j = 0; j < Object.keys(state.edit_questions[key]).length; j++) {
                        var keyOnDict = Object.keys(state.edit_questions[key])[j];
                        newEdit[key][keyOnDict] = state.edit_questions[key][keyOnDict];

                        if (keyOnDict == "answers") {
                            delete newEdit[key].answers[action.obj.id_answer]; 
                        }

                    }
                }
            }

            const newState11 = { status: state.status, id_survey: state.id_survey, questions_status: state.questions_status, edit_questions: newEdit, stats: state.stats }
            return newState11;

        case 'UPDATE_STATUS':
            const newState12 = { status: action.obj, id_survey: state.id_survey, questions_status: state.questions_status, edit_questions: state.questions_status, stats: state.stats  };
            return newState12;

        case 'UPDATE_STATS':

            var stats = {};

            console.log(action.obj);
            stats["id_survey"] = action.obj["_id"];
            stats["questions"] = {};

            action.obj["questions"].forEach(currentQuestion => {

                stats["questions"][currentQuestion["_id"]] = {}
                currentQuestion["answers"].forEach(currentAnswer => {
                    stats["questions"][currentQuestion["_id"]][currentAnswer["_id"]] = currentAnswer["value"];
                });

            });

            stats["status"] = true;
            
            const newState13 = { status: state.status, id_survey: state.id_survey, questions_status: state.questions_status, edit_questions: state.questions_status, stats: stats  };
            return newState13;

        default:
            return state;
    }
}
export default selectedReducer;


