import React from 'react';
import { Card, CardBody} from 'reactstrap';
import { connect } from 'react-redux';
import { Doughnut, HorizontalBar } from "react-chartjs-2";

class GraphicViewQuestion extends React.Component {

    constructor(props) {
        super(props);
        this.backgroundColor = [
            "#2ecc71",
            "#3498db",
            "#95a5a6",
            "#9b59b6",
            "#f1c40f",
            "#e74c3c",
            "#34495e"
        ],
/*         this.options= {
            title: {
                display: true,
                text: this.props.surveys[this.props.idSurvey].questions[this.props.idQuestion].txt,
                fontSize : 25
            },
            legend: {
                display: true,
                position: 'right'
            }
        } */
        
        this.loadVisual = this.loadVisual.bind(this);
    }


    loadVisual(){
        let chartData = {
            "labels": [],
            "datasets": [
                {
                    label : "answers",
                    data : [],
                    backgroundColor : []
                }
            ]
        }

        // patch visuel car pb pour les YesNo

        if (this.props.surveys[this.props.idSurvey].questions[this.props.idQuestion]["question_type"] != "YesNo") {
            for (var i = 0; i < Object.keys(this.props.surveys[this.props.idSurvey].questions[this.props.idQuestion].answers).length; i++) {
                var key = Object.keys(this.props.surveys[this.props.idSurvey].questions[this.props.idQuestion].answers)[i];
                chartData.labels.push(this.props.surveys[this.props.idSurvey].questions[this.props.idQuestion].answers[key].txt);
                if (this.props.stats.status) {
                    chartData.datasets[0].data.push(this.props.stats.questions[this.props.idQuestion][key]);
                }
                chartData.datasets[0].backgroundColor.push(this.backgroundColor[i]);
            }
        } else {
            for (var i = 0; i < Object.keys(this.props.surveys[this.props.idSurvey].questions[this.props.idQuestion].answers).length; i++) {
                var key = Object.keys(this.props.surveys[this.props.idSurvey].questions[this.props.idQuestion].answers)[i];
                if (["Yes", "No"].includes(this.props.surveys[this.props.idSurvey].questions[this.props.idQuestion].answers[key].txt)) {
                    chartData.labels.push(this.props.surveys[this.props.idSurvey].questions[this.props.idQuestion].answers[key].txt);
                    if (this.props.stats.status) {
                        chartData.datasets[0].data.push(this.props.stats.questions[this.props.idQuestion][key]);
                    }
                    chartData.datasets[0].backgroundColor.push(this.backgroundColor[i]);
                }
            }
        }

        if (this.props.surveys[this.props.idSurvey].questions[this.props.idQuestion]["question_type"] == "multiple") {
            var options = {
                title: {
                    display: true,
                    text: this.props.surveys[this.props.idSurvey].questions[this.props.idQuestion].txt,
                    fontSize: 25
                },
                scales: {
                    xAxes: [{
                        ticks: {
                            beginAtZero: true
                        }
                    }]
                },
                legend: {
                    display: false,
                    position: 'right'
                }
            }

            return (<HorizontalBar data={chartData} options={options} />);
        } else {
            var options = {
                title: {
                    display: true,
                    text: this.props.surveys[this.props.idSurvey].questions[this.props.idQuestion].txt,
                    fontSize: 25
                },
                legend: {
                    display: true,
                    position: 'right'
                }
            }
            return (<Doughnut data={chartData} options={options} />);
        }
    }

    render() {
        
        let visual = this.loadVisual();
        return (
            <Card>
                <CardBody>
                    {visual}
                </CardBody>
            </Card>

        );
    }
}

const mapStateToProps = (state, ownProps) => {
    console.log(state);
    return {
        surveys: state.updateModelReducer.surveys,
        stats: state.selectedReducer.stats
    }
};

export default connect(mapStateToProps)(GraphicViewQuestion);


