import React from 'react';
import './main.css';
import '../../lib/bootstrap-3.3.7-dist/css/bootstrap.min.css';
import * as surveyTmp from '../../source/sondages.json';
import BrowseSurveyPanel from '../browseSurveyPanel/containers/BrowseSurveyPanel';
import ShowSurveyPanel from '../showSurveyPanel/containers/ShowSurveyPanel';


import { Provider } from 'react-redux';
import { createStore } from 'redux';
import globalReducer from '../../reducers/';
import { updateSurvey } from '../../actions';
import { getSurveys } from '../../actions';
import { initSelection } from '../../actions';
var Comm = require('../../services/Comm.js');

const store = createStore(globalReducer);

export default class Main extends React.Component {
    constructor(props) {
        super(props);
        this.comm = new Comm();

        this.loadSurveyUpdate = this.loadSurveyUpdate.bind(this);
        this.callbackErr = this.callbackErr.bind(this);
        
        store.dispatch(initSelection());
        this.comm.loadSurvey(this.loadSurveyUpdate, this.callbackErr);
   
    }

    loadSurveyUpdate(data) {
        store.dispatch(updateSurvey(data));
    }


    callbackErr(msg) {
        console.error('Network Failure ?');
        console.error(msg);

    }


    render() {
        return (
            <Provider store={store} >
                <div className='container-fluid height-100'>

                    <nav class="navbar navbar-default">
                        <div class="container-fluid">
                            <div class="navbar-header">
                                <a class="navbar-brand" href="#">Capitalyze Your Time</a>
                            </div>
                        </div>


                    </nav>
                    <div className="row height-100">
                        <div className='col-md-3 col-lg-3 height-100 vertical-scroll'>
                            <BrowseSurveyPanel/>
                        </div>
                        <div className='col-md-9 col-lg-9 height-100 vertical-scroll'>
                            <ShowSurveyPanel/>
                        </div>
                    </div>
                </div>
                
            </Provider>
        );
    }
}


