import React, { Component } from 'react';
import { connect } from 'react-redux';
import { updateAnswersSelectedQuestion, addAnswerSelectedQuestion, deleteAnswerSelectedQuestion } from '../../../../actions';


class EditMetaAnswerString extends Component {
    //class constructor whith given properties
    constructor(props) {
        super(props);
        this.getAnswers=this.getAnswers.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.addAnswer = this.addAnswer.bind(this);
        this.deleteAnswer = this.deleteAnswer.bind(this);
    }

    handleChange(name,e) {
        this.props.dispatch(updateAnswersSelectedQuestion({ id_question: this.props.idQuestion, key: name, txt: e.target.value}));
    }

    addAnswer(){
        this.props.dispatch(addAnswerSelectedQuestion({ id_question: this.props.idQuestion}));
    }

    deleteAnswer(idAnswer, e) {
        console.log(idAnswer);
        console.log(e.target.value);
        this.props.dispatch(deleteAnswerSelectedQuestion({ id_question: this.props.idQuestion, id_answer: idAnswer }));
    }

    getAnswers(){
        let array_render = [];
        for (var i = 0; i < Object.keys(this.props.questions[this.props.idQuestion].answers).length; i++) {
            var key = Object.keys(this.props.questions[this.props.idQuestion].answers)[i];

            array_render.push(
                <div class="list-group-item">
                    <div class="input-group">
                        <input type="text" name='txt' value={this.props.questions[this.props.idQuestion].answers[key].txt} onChange={this.handleChange.bind(this, key)} class="form-control" placeholder="Answer" />

                        <span class="input-group-btn">
                                <button type="button" class="btn btn-danger glyphicon glyphicon-trash" onClick={this.deleteAnswer.bind(this, this.props.questions[this.props.idQuestion].answers[key].id_answer)}></button>
                        </span>

                    </div>
                </div>
            );

        }
        return array_render;
    }

    render() {
        let render = this.getAnswers();
        return (
            <div>
                {render}
                <div class="list-group-item">
                    <button type="button" class="btn btn-primary btn-block" onClick={this.addAnswer}>+</button>
                </div>
            </div>

        );
    }
}

const mapStateToProps = (state, ownProps) => {
    console.log(state);
    return {
        questions: state.selectedReducer.edit_questions,
    }
};

export default connect(mapStateToProps)(EditMetaAnswerString);


