import React, { Component } from 'react';
import { connect } from 'react-redux';


class AnswerString extends Component {
    //class constructor whith given properties
    constructor(props) {
        super(props);

        this.getAnswer = this.getAnswer.bind(this);
    }

    getAnswer(){
        let array_render = [];

        for (var i = 0; i < Object.keys(this.props.surveys[this.props.idSurvey].questions[this.props.idQuestion].answers).length; i++) {
            var key = Object.keys(this.props.surveys[this.props.idSurvey].questions[this.props.idQuestion].answers)[i];

            array_render.push(
                <div class="list-group-item">{this.props.surveys[this.props.idSurvey].questions[this.props.idQuestion].answers[key].txt}</div>
            );

        }
        return array_render;

    }
    
    render() {

        let array_render = this.getAnswer();

        return (
            <div class="list-group" >
                {array_render}
            </div>
        );
    }
}
const mapStateToProps = (state, ownProps) => {
    console.log(state);
    return {
        surveys: state.updateModelReducer.surveys,
    }
};

export default connect(mapStateToProps)(AnswerString);


