import React from 'react';
import Question from '../../question/containers/Question';
import { connect } from 'react-redux';
import { initSelection} from '../../../../actions';


class QuestionList extends React.Component {

    constructor(props) {
        super(props);
        this.getAllQuestionRender = this.getAllQuestionRender.bind(this);
    }


    getAllQuestionRender() {
        let array_render = [];

        for (var i = 0; i < Object.keys(this.props.surveys[this.props.idSurvey].questions).length; i++) {
            var key = Object.keys(this.props.surveys[this.props.idSurvey].questions)[i];
            //console.log("idQuestion: ".key);
            array_render.push(
                <Question idSurvey={this.props.idSurvey} displayMode={this.props.displayMode} idQuestion={key} />
            );

        }

        return array_render;
    }

    render() {
        const display_list = this.getAllQuestionRender();
        return (
            <div>
                {display_list}
            </div>
        );
    }
}

const mapStateToProps = (state, ownProps) => {
    console.log(state);
    return {
        surveys: state.updateModelReducer.surveys,
    }
};

export default connect(mapStateToProps)(QuestionList);


