import React, { Component } from 'react';
import Survey from '../../common/survey/containers/Survey';
import Dashboard from '../../dashboard/containers/Dashboard';
import { connect } from 'react-redux';
import { updateStatus, updateStats } from '../../../actions';
var Comm = require('../../../services/Comm.js');



class ShowSurveyPanel extends Component {
    //class constructor whith given properties
    constructor(props) {
        super(props);
        this.comm = new Comm();

        this.getCurrentSurvey = this.getCurrentSurvey.bind(this);
        this.changeStatusEdition = this.changeStatusEdition.bind(this);
        this.changeStatusResume = this.changeStatusResume.bind(this);
        this.loadStatsUpdate = this.loadStatsUpdate.bind(this);
        this.callbackErr = this.callbackErr.bind(this);
        
    }

    changeStatusResume(){
        this.props.dispatch(updateStatus("resume"));
        this.comm.getStatsSurvey({ "id_survey": this.props.idSurvey }, this.loadStatsUpdate, this.callbackErr);
    }

    changeStatusEdition(){
        this.props.dispatch(updateStatus("edit"));
    }

    getCurrentSurvey(){
        let current_survey = (<Dashboard/>);

        switch (this.props.selectedStatus) {
            case "dashbord":
                
                break;

            case "edit":
                current_survey = (
                    <div>
                        <ul class="nav nav-tabs nav-justified">
                            <li role="presentation" class="active"><a href="#">Edition</a></li>
                            <li role="presentation"><a href="#" onClick={this.changeStatusResume}>Resume <span class="badge">{this.props.surveys[this.props.idSurvey].nb_answers}</span></a></li>
                        </ul>
                        <Survey idSurvey={this.props.idSurvey} displayMode={"full"} />
                    </div>
                );
                break;

            case "resume":
                current_survey = (
                    <div>
                        <ul class="nav nav-tabs nav-justified">
                            <li role="presentation"><a href="#" onClick={this.changeStatusEdition}>Edition</a></li>
                            <li role="presentation" class="active"><a href="#">Resume <span class="badge">{this.props.surveys[this.props.idSurvey].nb_answers}</span></a></li>
                        </ul>
                        <Survey idSurvey={this.props.idSurvey} displayMode={"resume"} />
                    </div>
                );
                break;
        
            default:
                break;
        }

        return current_survey;

    }

    loadStatsUpdate(data) {
        this.props.dispatch(updateStats(data));
    }


    callbackErr(msg) {
        console.error('Network Failure ?');
        console.error(msg);
    }

    //render function use to update the virtual dom
    render() {
        let current_survey = this.getCurrentSurvey()
        
        return (
            <div>
                {current_survey}
            </div>
        );
    }
}



const mapStateToProps = (state, ownProps) => {
    console.log(state.selectedReducer.status);
    if (state.selectedReducer.status){
        // un sondage a été selectionné
        return {
            surveys: state.updateModelReducer.surveys,
            selectedStatus: state.selectedReducer.status,
            idSurvey: state.selectedReducer.id_survey
        }
    }
    else{
        // aucun sondage a été selectionné
        return {
            selectedStatus: state.selectedReducer.status,
        }
    }
};

export default connect(mapStateToProps)(ShowSurveyPanel);


