import React, { Component } from 'react';
import Visual from '../components/Visual';
import QuestionList from '../components/QuestionList';
import ActionAddQuestion from '../components/ActionAddQuestion';
import EditMetaSurvey from '../components/EditMetaSurvey';
import { connect } from 'react-redux';

class Survey extends Component {
    //class constructor whith given properties
    constructor(props) {
        super(props);
       
    }

    getStructure(){
        let structure = [];
        structure.push(
            <Visual
                idSurvey={this.props.idSurvey}
                displayMode={this.props.displayMode}
            />
        );

        if (this.props.displayMode === "full") {

            var addQuestionButton = ""

            if (this.props.surveys[this.props.idSurvey].status === "offline") {
                addQuestionButton=(<ActionAddQuestion idSurvey={this.props.idSurvey} />);
            }

            structure.push(
                <div className="panel panel-default">
                    <EditMetaSurvey idSurvey={this.props.idSurvey} />
                    {addQuestionButton}
                </div>
            );
        }

        if (["full", "resume"].includes(this.props.displayMode)) {
            structure.push(
                <QuestionList idSurvey={this.props.idSurvey} displayMode={this.props.displayMode}/>
            );
        }

        return structure;

    }


    render() {

        let render_visual = this.getStructure();

        return (
            <div>
                {render_visual}
            </div>
        );

    }
}


const mapStateToProps = (state, ownProps) => {
    console.log(state);
    return {
        surveys: state.updateModelReducer.surveys,
    }
};

export default connect(mapStateToProps)(Survey);


