import React, { Component } from 'react';
import AbstractAnswer from '../components/AbstractAnswer';
import Answer from '../../answer/containers/Answer';
import { addQuestion, initTempQuestion, saveQuestionStatus, updateSurvey, updateStats } from '../../../../actions';
import { connect } from 'react-redux';
import { Card, CardTitle, CardBody, Button, CardText, Row, Col } from 'reactstrap';

var Comm = require('../../../../services/Comm.js');

class NewMetaQuestion extends Component {
    //class constructor whith given properties
    constructor(props) {
        super(props);
        this.comm = new Comm();

        this.addQuestion = this.addQuestion.bind(this);
        this.loadSurveyUpdate = this.loadSurveyUpdate.bind(this);
        this.callbackErr = this.callbackErr.bind(this);

    }

    addQuestion() {

        let newQuestion = {
            id_survey: this.props.idSurvey,
            txt: this.props.tempQuestion,
            type: this.props.tempTypeQuestion,
            answers: this.props.tempAnswers
        };

        var position = Object.keys(this.props.surveys[this.props.idSurvey].questions).length;

        this.comm.addQuestion({"question": newQuestion, "position": position}, this.loadSurveyUpdate, this.callbackErr)
        //this.props.dispatch(addQuestion(newQuestion));
        
    }

    loadSurveyUpdate(data) {
        this.props.dispatch(updateSurvey(data));
        this.props.dispatch(saveQuestionStatus({ "id": this.props.idSurvey, "key_questions": Object.keys(this.props.surveys[this.props.idSurvey].questions) }));
        this.props.dispatch(initTempQuestion());
        this.props.dispatch(updateStats(this.props.idSurvey));
    }


    callbackErr(msg) {
        console.error('Network Failure ?');
        console.error(msg);

    }

    render() {
        return (
            <div>
                <div class="panel-body">
                    <div class="col-sm-9">
                        <Card body>
                            <CardTitle>{this.props.tempQuestion}</CardTitle>
                        </Card>
                    </div>
                    <div class="col-sm-3">
                        <Card body>
                            <CardText>Type : {this.props.tempTypeQuestion}</CardText>
                        </Card>
                    </div>
                </div>

                <div class="panel panel-default">
                    <div class="panel-heading">
                        <h3 class="panel-title">Answers</h3>
                    </div>
                    <Answer mode="new" />
                </div>
                <button type="button" class="btn btn-primary" onClick={this.addQuestion}>Add</button>
            </div>

        );

    }
}

const mapStateToProps = (state, ownProps) => {
    console.log(state);
    return {
        surveys: state.updateModelReducer.surveys,
        tempQuestion: state.tempQuestionReducer.question,
        tempTypeQuestion: state.tempQuestionReducer.type,
        tempAnswers: state.tempQuestionReducer.answers,

    }
};

export default connect(mapStateToProps)(NewMetaQuestion);


